import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment.prod';

@Injectable()
export class IndexService {
    constructor(private http: HttpClient) { }

    getNewsNotices() {
        return this.http.get(environment.url + '/news-notices');
    }

    getWelcome() {
        return this.http.get(environment.url + '/welcome');
    }

    getOfficials() {
        return this.http.get(environment.url + '/officials');
    }

    getApplicationFormat() {
        return this.http.get(environment.url + '/egov-services-file')
    }

    getEgovernmentServices() {
        return this.http.get(environment.url + '/egov-services')
    }

    getPlanAndProjects() {
        return this.http.get(environment.url + '/act-law')
    }

    getLinks() {
        return this.http.get(environment.url + '/links')
    }

    getEverestLives() {
        return this.http.get(environment.url + '/everest-lives')
    }

    getIntroduction() {
        return this.http.get(environment.url + '/introduction');
    }

}