import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment.prod';

@Injectable()
export class HeaderService {
    constructor(private http: HttpClient) { }

    getMountains() {
        return this.http.get(environment.url + '/mountains');
    }

    getIntroductions() {
        return this.http.get(environment.url + '/introduction');
    }

    getReports() {
        return this.http.get(environment.url + '/reports');
    }

    getActLaw() {
        return this.http.get(environment.url + '/act-law');
    }

    getTravelAndTourism() {
        return this.http.get(environment.url + '/travel-and-tourism');
    }

    getEgovServices() {
        return this.http.get(environment.url + '/egov-services');
    }
}