import { Component, OnInit } from '@angular/core';
import { HttpClient} from '@angular/common/http';
import  { TrekkingService } from './trekking-service';

@Component({
  selector: 'app-trekking',
  templateUrl: './trekking.component.html',
  styleUrls: ['./trekking.component.scss'],
  providers: [TrekkingService]
})
export class TrekkingComponent implements OnInit {
  trekking = [];
  constructor(private http: HttpClient, private service : TrekkingService) { 
    this.service.getTrekking().subscribe((data: any)=> {
      this.trekking = data;
    });
  }
  ngOnInit() {
  }

}
