import { Component, OnInit } from '@angular/core';
import { HttpClient} from '@angular/common/http';
import  { PassportService } from './passport-service';

@Component({
  selector: 'app-password-and-visa',
  templateUrl: './password-and-visa.component.html',
  styleUrls: ['./password-and-visa.component.scss'],
  providers: [PassportService]
})
export class PasswordAndVisaComponent implements OnInit {
  passport = [];
  constructor(private http: HttpClient, private service : PassportService) { 
    this.service.getPassport().subscribe((data: any)=> {
      this.passport = data;
    });
  }
  ngOnInit() {
  }

}
