import { Component, OnInit } from '@angular/core';
import { TouristInformationService } from '../travel-and-tourism-information/travel-and-tourism-information.service';
import { LocalStorageService } from '../_services/localstorage.service';
import { environment } from './../../environments/environment';
import { Router } from '@angular/router';

@Component({
  selector: 'app-travel-and-tourism',
  templateUrl: './travel-and-tourism.component.html',
  styleUrls: ['./travel-and-tourism.component.scss'],
  providers: [TouristInformationService]
})
export class TravelAndTourismComponent implements OnInit {

  baseUrl:any;
  travelList:any;

  constructor(
    private service: TouristInformationService,
    private localStorageService: LocalStorageService,
    private router: Router,

  ) { 

    this.baseUrl = environment.url;

    this.getTravelAndTourism();
  }

  ngOnInit() {
  }

  get lanValue() {
    return this.localStorageService.getLanguage()
  }

  getTravelAndTourism(){
    this.service.getTravelAndTourism().subscribe((response)=>{
      this.travelList = response;
    });
  }

  displayTravelAndTourism(event: MouseEvent, id) {
    event.preventDefault();
    this.router.navigate(['/travel-and-tourism-information', id]);
  }

}
