import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { MountainService } from './mountain-service';
import { Router, Params } from '@angular/router';
import { environment } from './../../environments/environment';
import { LocalStorageService } from '../_services/localstorage.service';

@Component({
  selector: 'app-mt-everest',
  templateUrl: './mountain.component.html',
  styleUrls: ['./mountain.component.scss'],
  providers: [MountainService]
})

export class MountainComponent implements OnInit {
  id: number;
  mountain = [];
  environment = environment;
  constructor(private http: HttpClient,
     private service: MountainService,
     private activatedRoute: ActivatedRoute,
     private router: Router,
     private localStorageService: LocalStorageService) {

    this.activatedRoute.params.subscribe((params: Params) => {
      let id = params['id'];
      this.service.getMountainById(id).subscribe((data: any) => {
        this.mountain = data;
      })
    });
  }

  ngOnInit() {
  }

  get lanValue() {
    return this.localStorageService.getLanguage()
  }

}
