import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { HeaderService } from './header-service';
import { Router } from '@angular/router';
import { environment } from './../../environments/environment';
import { LocalStorageService } from '../_services/localstorage.service';
import { LanguageService } from '../_services/language.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
  providers: [HeaderService, LocalStorageService, LanguageService]
})
export class HeaderComponent implements OnInit {
  mountains = [];
  introductions = [];
  reports = [];
  acts = [];
  travelTourism = [];
  egovServices = [];
  environment = environment;
  constructor(private http: HttpClient,
    private service: HeaderService,
    private router: Router,
    private localStorageService: LocalStorageService,
    private languageService: LanguageService) {

      // this.localStorageService.setLanguage('en');

    this.service.getMountains().subscribe((data: any) => {
      this.mountains = data;
    });

    this.service.getIntroductions().subscribe((data: any) => {
      this.introductions = data;
    });

    this.service.getReports().subscribe((data: any) => {
      this.reports = data;
    });

    this.service.getActLaw().subscribe((data: any) => {
      this.acts = data;
    });

    this.service.getTravelAndTourism().subscribe((data: any) => {
      this.travelTourism = data;
    });

    this.service.getEgovServices().subscribe((data: any) => {
      this.egovServices = data;
    });
  }

  ngOnInit() {
  }

  get lanValue() {
    return this.localStorageService.getLanguage()
  }

  displayMountain(event: MouseEvent, id) {
    event.preventDefault();
    this.router.navigate(['/mountain', id]);
  }

  displayIntroduction(event: MouseEvent, id) {
    event.preventDefault();
    this.router.navigate(['/brief-introduction', id]);
  }

  displayActLaw(event: MouseEvent, id) {
    event.preventDefault();
    this.router.navigate(['/act-law', id]);
  }

  displayTravelAndTourism(event: MouseEvent, id) {
    event.preventDefault();
    this.router.navigate(['/travel-and-tourism-information', id]);
  }

  displayEgovServices(event: MouseEvent, id) {
    event.preventDefault();
    this.router.navigate(['/egov-service', id]);
  }

  getLanguague(value){
    this.languageService.setLanguageValue(value);
  }
}
