import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { FooterService } from './footer-service';
import { Router } from '@angular/router';
import { environment } from './../../environments/environment';
import { LocalStorageService } from '../_services/localstorage.service';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.scss'],
  providers: [FooterService]
})
export class FooterComponent implements OnInit {
  contactDetails = [];
  socialMedia = [];
  copyright = [];
  news = [];
  egovServices = [];
  reports = [];
  introductions = [];
  environment = environment;
  baseUrl: any;

  constructor(private http: HttpClient,
     private service: FooterService,
     private router: Router,
     private localStorageService: LocalStorageService
     ) {

    this.baseUrl = environment.url;

    this.service.getIntroductions().subscribe((data: any) => {
      this.introductions = data;
    });

    this.service.getContactDetails().subscribe((data: any) => {
      this.contactDetails = data;
    });

    this.service.getSocialMedia().subscribe((data: any) => {
      this.socialMedia = data;
    });

    this.service.getCopyright().subscribe((data: any) => {
      this.copyright = data;
    });

    this.service.getNews().subscribe((data: any) => {
      this.news = data.reverse();
    });

    this.service.getEgovServices().subscribe((data: any) => {
      this.egovServices = data;
    });

    this.service.getReports().subscribe((data: any) => {
      this.reports = data;
    });
  }
  ngOnInit() {
  }

  get lanValue() {
    return this.localStorageService.getLanguage()
  }

  displayEgovServices(event: MouseEvent, id:number) {
    event.preventDefault();
    document.body.scrollTop = document.documentElement.scrollTop = 0;
    this.router.navigate(['/egov-service', id]);
  }

  displayIntroduction(event: MouseEvent, id) {
    event.preventDefault();
    document.body.scrollTop = document.documentElement.scrollTop = 0;
    this.router.navigate(['/brief-introduction', id]);
  }

  displayWard(){
    document.body.scrollTop = document.documentElement.scrollTop = 0;
    this.router.navigate(['/ward-structure']);
  }

  displayVillage(){
    document.body.scrollTop = document.documentElement.scrollTop = 0;
    this.router.navigate(['/village-profile']);
  }

  displayTravelAndTourism(){
    document.body.scrollTop = document.documentElement.scrollTop = 0;
    this.router.navigate(['/travel-and-tourism']);
  }

  displayMountain(){
    document.body.scrollTop = document.documentElement.scrollTop = 0;
    this.router.navigate(['/mountain-list']);
  }

  displayNotices(){
    document.body.scrollTop = document.documentElement.scrollTop = 0;
    this.router.navigate(['/news-notice']);
  }

  displayReports(){
    document.body.scrollTop = document.documentElement.scrollTop = 0;
    this.router.navigate(['/reports']);
  }
}
