import { Component, OnInit } from '@angular/core';
import { HttpClient} from '@angular/common/http';
import  { LocalactService } from './localact-service';
import { environment } from './../../environments/environment';

@Component({
  selector: 'app-local-act-law-and-directives',
  templateUrl: './local-act-law-and-directives.component.html',
  styleUrls: ['./local-act-law-and-directives.component.scss'],
  providers: [LocalactService]
})
export class LocalActLawAndDirectivesComponent implements OnInit {
  localact = [];
  environment = environment;
  constructor(private http: HttpClient, private service : LocalactService) { 
    this.service.getLocalAct().subscribe((data: any)=> {
      this.localact = data;
    });
  }
  ngOnInit() {
  }

}
