import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment.prod';

@Injectable()
export class FooterService {
    constructor(private http: HttpClient) { }

    getContactDetails() {
        return this.http.get(environment.url + '/contact-details');
    }

    getSocialMedia() {
        return this.http.get(environment.url + '/social-media');
    }

    getCopyright() {
        return this.http.get(environment.url + '/copyright');
    }

    getNews() {
        return this.http.get(environment.url + '/news-notices');
    }

    getEgovServices() {
        return this.http.get(environment.url + '/egov-services');
    }

    getReports() {
        return this.http.get(environment.url + '/reports');
    }

    getIntroductions() {
        return this.http.get(environment.url + '/introduction');
    }
}