import { Component, OnInit } from '@angular/core';
import { MountainService } from '../mountain/mountain-service';
import { LocalStorageService } from '../_services/localstorage.service';
import { environment } from './../../environments/environment';
import { Router } from '@angular/router';

@Component({
  selector: 'app-mountain-list',
  templateUrl: './mountain-list.component.html',
  styleUrls: ['./mountain-list.component.scss'],
  providers: [MountainService]
})
export class MountainListComponent implements OnInit {

  baseUrl:any;
  mountainList:any;

  constructor(
    private service: MountainService,
    private localStorageService: LocalStorageService,
    private router: Router,
  ) { 

    this.baseUrl = environment.url;

    this.getMountain();

  }

  ngOnInit() {
  }

  get lanValue() {
    return this.localStorageService.getLanguage()
  }

  getMountain(){
    this.service.getMountain().subscribe((response)=>{
      this.mountainList = response;
    });
  }

  displayMountain(event: MouseEvent, id) {
    event.preventDefault();
    this.router.navigate(['/mountain', id]);
  }

}
