import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment.prod';

@Injectable()
export class TouristInformationService {
    constructor(private http: HttpClient) { }

    getTravelAndTourismById(travelAndTourismId) {
        return this.http.get(environment.url + '/travel-and-tourism/' + travelAndTourismId);
    }

    getTravelAndTourism() {
        return this.http.get(environment.url + '/travel-and-tourism');
    }
}