import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { IndexService } from './index-service';
import { Router } from '@angular/router';
import { environment } from './../../environments/environment';
import { LocalStorageService } from '../_services/localstorage.service';
declare var $;

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss'],
  providers: [IndexService, LocalStorageService]
})
export class IndexComponent implements OnInit {

  @ViewChild('dataTable') table: ElementRef;
  dataTable: any;



  newsNotices:any;
  latestNewsNotices:any;
  latestEngNews:any;
  latestNepNews:any;
  welcome = [];
  officialsList:any;
  introductionList:any;
  applicationFormat = [];
  governmentServices = [];
  planProjects = [];
  links = [];
  everestLives:any;
  environment = environment;
  baseUrl:any;

  constructor(private http: HttpClient, private service: IndexService, private router: Router,
    private localStorageService: LocalStorageService) {

    this.baseUrl = environment.url;

    this.service.getNewsNotices().subscribe((data: any) => {
      if(data){
      this.newsNotices = data.reverse();
    }
     
    });

    this.service.getWelcome().subscribe((data: any) => {
      this.welcome = data;
    });

    this.service.getOfficials().subscribe((data: any) => {
      this.officialsList = data.reverse();
    });

    this.service.getApplicationFormat().subscribe((data: any) => {
      this.applicationFormat = data;
    });

    this.service.getEgovernmentServices().subscribe((data: any) => {
      this.governmentServices = data;
    });

    this.service.getPlanAndProjects().subscribe((data: any) => {
      this.planProjects = data;
    });

    this.service.getLinks().subscribe((data: any) => {
      this.links = data;
    });

    this.service.getEverestLives().subscribe((data: any) => {
      this.everestLives = data;
    });

    this.service.getIntroduction().subscribe((data: any) => {
      this.introductionList = data;
    });
  }
  ngOnInit() {
    // this.dataTable = $(this.table.nativeElement);
    // this.dataTable.dataTable();

  /*
    this.companyService.getCompanies()
     .subscribe((company: Company[]) => {
        this.companies = company;
        // You'll have to wait that changeDetection occurs and projects data into
        // the HTML template, you can ask Angular to that for you ;-)
        this.chRef.detectChanges();
        // Now you can use jQuery DataTables :
        const table: any = $('datatables');
        this.dataTable = table.DataTable();
     });
  */
  }

  get lanValue() {
    return this.localStorageService.getLanguage()
  }

  displayNews(event: MouseEvent, id) {
    event.preventDefault();
    this.router.navigate(['/news-notice-detail', id]);
  }

  displayActLaw(event: MouseEvent, id) {
    event.preventDefault();
    this.router.navigate(['/act-law', id]);
  }

  displayEgovServices(event: MouseEvent, id) {
    event.preventDefault();
    this.router.navigate(['/egov-service', id]);
  }

  displayIntroduction(event: MouseEvent, id) {
    event.preventDefault();
    document.body.scrollTop = document.documentElement.scrollTop = 0;
    this.router.navigate(['/brief-introduction', id]);
  }

  getNews(id:number){
    console.log(id);
    this.router.navigate(['/news-notice-detail', id]);
  }
}
