import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { EgovService } from './egov-service-service';
import { ActivatedRoute, Router, Params } from '@angular/router';
import { environment } from './../../environments/environment';
import { LocalStorageService } from '../_services/localstorage.service';

@Component({
  selector: 'app-egov-service',
  templateUrl: './egov-service.component.html',
  styleUrls: ['./egov-service.component.scss'],
  providers: [EgovService]
})
export class EgovServiceComponent implements OnInit {

  environment = environment;
  egovServices = [];
  id: number;
  baseUrl:any;

  constructor(private http: HttpClient,
     private service: EgovService,
     private activatedRoute: ActivatedRoute,
     private router: Router,
     private localStorageService: LocalStorageService
     ) {

    this.activatedRoute.params.subscribe((params: Params) => {
      let id = params['id'];
      this.service.getEgovServicesFile(id).subscribe((data: any) => {
        this.egovServices = data.reverse();
      })
    });

    this.baseUrl = environment.url;

  }


  ngOnInit() {
  }

  get lanValue() {
    return this.localStorageService.getLanguage()
  }

}
