import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { TouristInformationService } from './travel-and-tourism-information.service';
import { ActivatedRoute, Router, Params } from '@angular/router';
import { environment } from './../../environments/environment';
import { LocalStorageService } from '../_services/localstorage.service';

@Component({
  selector: 'app-travel-and-tourism-information',
  templateUrl: './travel-and-tourism-information.component.html',
  styleUrls: ['./travel-and-tourism-information.component.scss'],
  providers: [TouristInformationService]
})
export class TravelAndTourismInformationComponent implements OnInit {
  travelAndTourism = [];
  environment = environment;
  constructor(private http: HttpClient,
     private service: TouristInformationService,
     private activatedRoute: ActivatedRoute,
     private router: Router,
     private localStorageService: LocalStorageService
     ) {

    this.activatedRoute.params.subscribe((params: Params) => {
      let id = params['id'];
      this.service.getTravelAndTourismById(id).subscribe((data: any) => {
        this.travelAndTourism = data;
      })
    });
  }
  
  ngOnInit() {
  }

  get lanValue() {
    return this.localStorageService.getLanguage()
  }

}
