import { Component, OnInit } from '@angular/core';
import { HttpClient} from '@angular/common/http';
import  { ActLawService } from './actlaw-service';
import { ActivatedRoute, Router, Params } from '@angular/router';
import { environment } from './../../environments/environment';
import { LocalStorageService } from '../_services/localstorage.service';

@Component({
  selector: 'app-act-law',
  templateUrl: './act-law.component.html',
  styleUrls: ['./act-law.component.scss'],
  providers: [ActLawService]
})
export class ActLawComponent implements OnInit {
  id: number;
  actLaw = [];
  centralact = [];
  environment = environment;

  constructor(private http: HttpClient,
     private service : ActLawService,
     private activatedRoute: ActivatedRoute,
     private router: Router,
     private localStorageService: LocalStorageService
     ) { 

    this.activatedRoute.params.subscribe((params: Params) => {
      let id = params['id'];
      this.service.getActLawById(id).subscribe((data: any) => {
        this.actLaw = data.reverse();
      })
    });
  }
  
  ngOnInit() {
  }

  get lanValue() {
    return this.localStorageService.getLanguage()
  }

}
