import { Component, OnInit } from '@angular/core';
import { HttpClient} from '@angular/common/http';
import  { OrganizationStructureService } from './organization-structure.service';

@Component({
  selector: 'app-organization-structure',
  templateUrl: './organization-structure.component.html',
  styleUrls: ['./organization-structure.component.scss'],
  providers: [OrganizationStructureService]
})
export class OrganizationStructureComponent implements OnInit {
  organizationStructure = [];
  constructor(private http: HttpClient, private service : OrganizationStructureService) { 
    this.service.getOrganizationStructure().subscribe((data: any)=> {
      this.organizationStructure = data;
    });
  }
  
  ngOnInit() {
  }

}
